import React from 'react'
import moment from 'moment'
import PropTypes from 'prop-types'
import Account from '../Account'
import AssetMetadata from './AssetMetadata'
import BundleMetadata from './BundleMetadata'
import styled from 'styled-components';
import { connectWallet, signMsg } from '../../constants';
import { OrderSide } from 'opensea-js/lib/types';
import SalePrice from '../common/SalePrice';

const Card = styled.div.attrs({ className: "card mx-2 mb-4" })`
  min-width: 200px;
  img {
    height: 120px;
    max-width: 100%;
  }
  img.small {
    max-width: 50%;
    height: 60px;
  }
`

export default class Order extends React.Component {

  state = {
    errorMessage: null,
    creatingOrder: false
  }

  static propTypes = {
    currentAccount: PropTypes.object,
    order: PropTypes.shape({
      makerAccount: PropTypes.object.isRequired
    }).isRequired,
    seaport: PropTypes.object.isRequired,
    accountAddress: PropTypes.string
  }

  onError(error) {
    // Ideally, you'd handle this error at a higher-level component
    // using props or Redux
    this.setState({ errorMessage: error.message })
    setTimeout(() => this.setState({errorMessage: null}), 3000)
    throw error
  }

  async fulfillOrder() {
    const { order, accountAddress } = this.props
    if (!accountAddress) {
      await connectWallet()
    }
    try {
      this.setState({ creatingOrder: true })
      await this.props.seaport.fulfillOrder({ order, accountAddress })
    } catch(error) {
      this.onError(error)
    } finally {
      this.setState({ creatingOrder: false })
    }
  }

  renderBuyButton(canAccept = true) {
    const { creatingOrder } = this.state
    const { accountAddress, order } = this.props
    const buyAsset = async () => {
      if (accountAddress && !canAccept) {
        this.setState({
          errorMessage: "You already own this asset!"
        })
        return
      }
      this.fulfillOrder()
    }
    return (
      <button
        disabled={creatingOrder}
        onClick={buyAsset}
        className="btn btn-primary w-100">
        
        Buy{creatingOrder ? "ing" : ""} for <SalePrice order={order} />

      </button>
    )
  }

  async verifyOwner() {
    const { order } = this.props
    let { accountAddress } = this.props
    const { asset } = order
    if (!accountAddress) {
      accountAddress = await connectWallet()
    }
    try {
      const balance = await this.props.seaport.getAssetBalance({
        accountAddress, // string
        asset, // Asset
      })
      
      const ownsAsset = balance.greaterThan(0);
      this.setState({
        errorMessage: ownsAsset.toString()
      })
    } catch(error) {
      this.onError(error)
    } finally {
      this.setState({ creatingOrder: false })
    }
  }

  renderVerifyOwnershipButton() {
    const verifyOwner = async () => {
      this.verifyOwner()
    }
    return (
      <button
        onClick={verifyOwner}
        className="btn btn-primary w-100">
        
        Do I own this?

      </button>
    )
  }

  async signatureVerifyOwner() {
    const { order } = this.props
    const { asset } = order
    const accountAddress = await connectWallet()

    try {
      const balance = await this.props.seaport.getAssetBalance({
        accountAddress, // string
        asset, // Asset
      })
      
      const ownsAsset = balance.greaterThan(0);

      const domain = [
        { name: "name", type: "string" },
        //{ name: "version", type: "string" },
        //{ name: "chainId", type: "uint256" },
        { name: "verifyingContract", type: "address" },
        //{ name: "salt", type: "bytes32" },
    ];
    const identity = [
        { name: "userId", type: "uint256" },
    ];

    const domainData = {
      name: "Artix App",
      //version: "2",
      //chainId: parseInt(web3.version.network, 10),
      verifyingContract: asset.tokenAddress,
      //salt: "0xf2d857f4a3edcb9b78b4d503bfe733db1e3f6cdc2b7971ee739626c97e86a558"
  };
  var message = {
      account: accountAddress,
      contract: asset.tokenAddress,
      tokenId: asset.tokenId,
      info: "Sign to verify that you own the selected NFT"
  };

      const data = {
        types: {
          EIP712Domain: domain,
          Identity: identity,
        },
        domain: domainData,
        primaryType: "Identity",
        message: message
      };
      const isSigner = signMsg(accountAddress, data)
      isSigner.then((signerAddress) => {
        this.setState({
          errorMessage: (ownsAsset && (signerAddress === accountAddress)).toString()
        })
      })
    } catch(error) {
      this.onError(error)
    } finally {
      this.setState({ creatingOrder: false })
    }
  }

  renderSignatureVerifyOwnershipButton() {
    const { creatingOrder } = this.state
    const { accountAddress, order } = this.props
    const signatureVerifyOwner = async () => {
      this.signatureVerifyOwner()
    }
    return (
      <button
        onClick={signatureVerifyOwner}
        className="btn btn-primary w-100">
        
        Sign and Verify

      </button>
    )
  }

  renderAcceptOfferButton(canAccept = true) {
    const { creatingOrder } = this.state
    const { accountAddress, order } = this.props
    
    const sellAsset = async () => {
      if (accountAddress && !canAccept) {
        this.setState({
          errorMessage: "You do not own this asset!"
        })
        return
      }
      this.fulfillOrder()
    }
    return (
      <button
        disabled={creatingOrder}
        onClick={sellAsset}
        className={`btn btn-success w-100`}>

        Sell{creatingOrder ? "ing" : ""} for <SalePrice order={order} />

      </button>
    )
  }

  renderClearMessageButton() {  
    const clearError = async () => {
      this.setState({
        errorMessage: null
      })
    }
    return (
      <button
        onClick={clearError}
        className={`btn btn-success w-100`}>

        Clear Message

      </button>
    )
  }

  renderExpirationBadge() {
    const expirationTime = parseFloat(this.props.order.expirationTime)

    if (expirationTime <= 0) {
      return null;
    }

    const timeLeft = moment.duration(moment.unix(expirationTime).diff(moment()))

    return (
      <span className="badge bid-expiry-badge red">
        <i className="tiny material-icons">timer</i>
        <span className="expire-label">Expires in </span>
        {timeLeft.humanize()}
      </span>
    )
  }

  render() {
    const { errorMessage } = this.state
    const { order, accountAddress } = this.props
    const { makerAccount, listingTime, asset, assetBundle } = order

    const owner = asset
      ? asset.owner
      : assetBundle.assets[0].owner

    const ts = listingTime.toNumber() * 1000
    const timeLabel = moment(ts).local().fromNow()
    const isOwner = accountAddress && accountAddress.toLowerCase() === owner.address.toLowerCase()

    return (
      <Card>
        
        {asset
          ? <AssetMetadata asset={asset} />
          : <BundleMetadata bundle={assetBundle} />
        }
        
        <ul className="list-group list-group-flush">
          <li className="list-group-item">
            Offered by <Account account={makerAccount} />
          </li>
          { errorMessage
            ? <div className="alert alert-warning mb-0" role="alert">
                {errorMessage}
                { this.renderClearMessageButton() }
              </div>
            : <li className="list-group-item">
                {order.side === OrderSide.Buy
                  ? this.renderAcceptOfferButton(isOwner)
                  : null
                }
                {order.side === OrderSide.Sell
                  ? this.renderBuyButton(!isOwner)
                  : null
                }
                { this.renderVerifyOwnershipButton() }
                { this.renderSignatureVerifyOwnershipButton() }
              </li>
          }
        </ul>
        <div className="card-footer">
          <small className="text-muted">Posted {timeLabel}</small>
        </div>
      </Card>
    )
  }
}
